  font-family: 'portfolio';
  src: url('../font/portfolio.eot?16987911');
  src: url('../font/portfolio.eot?16987911#iefix') format('embedded-opentype'),
       url('../font/portfolio.woff2?16987911') format('woff2'),
       url('../font/portfolio.woff?16987911') format('woff'),
       url('../font/portfolio.ttf?16987911') format('truetype'),
       url('../font/portfolio.svg?16987911#portfolio') format('svg');
  font-weight: normal;
  font-style: normal;
}
